<template>
  <div id="app">
    <CodeMirrorVue
        class="editor"
        v-model="code"
        :options="options"
        @ready="ready"
        @input="updateSelectCode($event)"
        ref="cmEditor"
    />
    <Tools class="tools" :side-options="supportType" :wait-result-flag="waitResultFlag"
           @runClick="getCompileResult"
    ></Tools>
    <CompileResult :result-data="result" :type="resultType" :wait-result-flag="waitResultFlag"/>
  </div>
</template>

<script>
import CompileResult from "@/components/CompileResult";
import CodeMirrorVue from "@/components/CodeMirrorVue";
import Tools from "@/components/Tools";

// codemirror 依赖
import "codemirror/lib/codemirror.css"
import "codemirror/addon/edit/matchbrackets"
import "codemirror/addon/selection/active-line"
import "codemirror/theme/darcula.css"
import "codemirror/addon/scroll/simplescrollbars"
import "codemirror/addon/scroll/simplescrollbars.css"
import "codemirror/addon/edit/closebrackets"
// koltin style dependency
import "codemirror/mode/clike/clike"


let compileMsg = {
  mode: "text/x-kotlin",
  local: "KOTLIN",
  defaultValue: "fun main(){\n\tprintln(\"hello world !!!\")\n}",
  analyzeError(result,instance) {
    let reg = /^main\.kt:(\d+):(\d+): (.+)$/gm
    for (let i of result){
      let res = i.matchAll(reg)
      // eslint-disable-next-line no-constant-condition
      while (1){
        let temp = res.next()
        if(temp.value!==undefined){
          instance.setGutterMarker(parseInt(temp.value[1]) - 1, temp.value[3])
          instance.setTextMarker({line: parseInt(temp.value[1]) - 1, ch: parseInt(temp.value[2]) - 1}, {line: parseInt(temp.value[1]), ch: 0})
        }else
        break;
      }
    }
  }
}

export default {
  name: 'App',
  components: {
    CompileResult,
    CodeMirrorVue,
    Tools
  },
  data() {
    return {
      code: this.checkSelect(),
      options: {
        mode: compileMsg.mode,
        lineNumbers: true,
        styleActiveLine: true,
        matchBrackets: true,
        autoCloseBrackets: true,
        viewportMargin: Infinity,
        theme: "darcula",
        tabSize: 2,
        scrollbarStyle: "overlay",
        gutters: ["CodeMirror-linenumbers", {className: "ErrorInfo", style: "width:28px"}]
      },
      result: ["Hello,Have a good time"],
      waitResultFlag: false,
      resultType: null,
      editor: null,
      supportType: null
    }
  },
  methods: {
    checkSelect() {
      let a = localStorage.getItem(compileMsg.local)
      return a ? a : compileMsg.defaultValue
    },
    updateSelectCode(S) {
      localStorage.setItem(compileMsg.local, S)
    },
    ready() {
      this.editor = this.$refs.cmEditor.codemirror
    },
    getGutterMarker(label) {
      let dom = document.createElement("div")
      dom.setAttribute('class', 'errorFlag')
      dom.innerHTML = "!<span class=\"errorTextTip\">" + label + "</span>"
      return dom
    },
    setGutterMarker(line, label = "error tip") {
      let dom = this.editor.lineInfo(line).gutterMarkers ? null : this.getGutterMarker(label)
      return this.editor.setGutterMarker(line, "ErrorInfo", dom)
    },
    setTextMarker(from, to) {
      let options = {
        className: 'textErrorMarker',
        css: "text-decoration: underline wavy #ec5424;color: #ec5424;"
      }
      let textMarker = this.editor.markText(from, to, options)
      let temp = textMarker.find()
      let old = this.editor.getRange(temp.from, temp.to)
      let a = setInterval(() => {
        let temp1 = textMarker.find()
        if (temp1 === undefined) clearInterval(a)
        if (this.editor.getRange(temp1.from, temp1.to) !== old) {
          textMarker.clear()
          for (let i = temp1.from.line; i <= temp1.to.line; i++) {
            if (this.editor.lineInfo(i).gutterMarkers) {
              this.editor.setGutterMarker(i, "ErrorInfo", null)
            }
          }
          clearInterval(a)
        }
      }, 500)
      return textMarker
    },
    getCompileTypeList() {
      return new Promise((resolve) => {
        this.axios.options("/compile").then((response) => {
          let data = response.data
          this.$set(this, "supportType", data)
          resolve(data)
        })
      })
    },
    getCompileResult() {
      this.waitResultFlag = true
      this.editor.clearGutter("ErrorInfo")
      this.editor.getAllMarks().forEach((it) => {
        it.clear()
      })
      this.axios.post("/compile",
          {
            code: this.code,
            compileType: compileMsg.local
          }
      ).then((response) => {
        let data = response.data;
        this.waitResultFlag = false;
        this.resultType = data.type;
        if (data.type === "SUCCESS") {
          this.$set(this, 'result', data.text)
        } else {
          this.$set(this, 'result', data.errorText)
        }
      })
    },
    // 高耦合,用正则为错误信息标注
    analyzeError() {
      compileMsg.analyzeError(this.result,this)
    }
  },
  created() {
    this.getCompileTypeList()
  },
  watch: {
    result() {
      if (this.resultType === "ERROR") {
        this.analyzeError()
      }
    }
  }
}
</script>
<style src="./css/App.vue.css"></style>
<style>
.errorFlag {
  width: 23px;
  height: 23px;
  line-height: 23px;
  text-align: center;
  background: red;
  border-radius: 100%;
  color: white;
  font-size: 15px;
  font-weight: bold;
}
.textErrorMarker {
  text-decoration: underline wavy #ec5424;
  color: #ec5424;
}
.errorTextTip {
  visibility: hidden;
  background-color: #fff;
  color: #ec5424;
  text-align: center;
  padding: 5px;
  border-radius: 6px;
  position: absolute;
  z-index: 1000;
  left: 24px;
  display: inline-flex;
  white-space: pre;
  margin-top: -10px;
  transition: all 300ms ease-in-out;
  font-family: 'Gotham SSm A', 'Gotham SSm B', 'Open Sans', Helvetica, Arial, sans-serif;
}
.errorFlag:hover > .errorTextTip {
  visibility: visible;
  margin-top: 0;
}
</style>
