<template>
  <div id="CompileResult" :class="{hide:!show}"
       :style="{height: 'calc(20vh '+getOffset+')'}"
  >
    <div class="topDrag" :class="{hide:!show}"
         @mousedown.prevent="dragDown($event)"
         @mousemove.prevent="dragMove($event)"
         @mouseup.prevent="dragUp($event)"
    >
      <div class="lines" :class="{hide:!show}">
        <div class="line"></div>
        <div class="line"></div>
        <div class="line"></div>
      </div>
    </div>
    <div class="output">
      <span class="output-span" :class="{standard: type==='SUCCESS',error: type==='ERROR'}"
            v-for="result in resultData" :key="result"
            :style="{display: hideOutPut}"
      >{{ result }}</span>
      <div class="overlay" :class="{show:this.waitResultFlag}">
        <svg t="1609484352102" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
             p-id="1183" width="200" height="200">
          <path
              d="M563.2 819.2a102.4 102.4 0 1 1 0 204.8 102.4 102.4 0 0 1 0-204.8z m-320.4608-153.6a128 128 0 1 1 0 256 128 128 0 0 1 0-256z m592.7936 25.6a102.4 102.4 0 1 1 0 204.8 102.4 102.4 0 0 1 0-204.8zM947.2 477.1328a76.8 76.8 0 1 1 0 153.6 76.8 76.8 0 0 1 0-153.6zM128 307.2a128 128 0 1 1 0 256 128 128 0 0 1 0-256z m782.6432-40.6016a51.2 51.2 0 1 1 0 102.4 51.2 51.2 0 0 1 0-102.4zM409.6 0a153.6 153.6 0 1 1 0 307.2 153.6 153.6 0 0 1 0-307.2z m384 153.6a25.6 25.6 0 1 1 0 51.2 25.6 25.6 0 0 1 0-51.2z"
              fill="#555555" p-id="1184"></path>
        </svg>
      </div>
    </div>
    <div class="floor">
      <p>© 2000-2020 FANICK</p>
    </div>
    <div class="closeButton" :class="{moveTop:!show}" @click="switchStatus">
      <div class="closeLine left" :style="{backgroundColor:btnColor}"></div>
      <div class="closeLine right" :style="{backgroundColor:btnColor}"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CompileResult",
  props: {
    resultData: {
      type: Array
    },
    type: {
      type: String
    },
    waitResultFlag: {
      type: Boolean,
      default: false,
    }
  },
  data() {
    return {
      show: false,
      offset: 0,
      dragFlag: false,
      dragBegin: 0,
      oldOffset: 0
    }
  },
  methods: {
    switchStatus() {
      this.show = !this.show
    },
    dragMove(e) {
      if (this.dragFlag) {
        let temp = this.dragBegin - e.clientY + this.oldOffset
        if(window.innerHeight-e.clientY>=50)
          this.offset = temp
      }
      return e
    },
    dragDown(e) {
      if (this.show){
        this.dragFlag = true
        this.dragBegin = e.clientY
      }
      return e
    },
    dragUp(e) {
      this.dragFlag = false
      this.oldOffset = this.offset
      return e
    }
  },
  computed: {
    hideOutPut() {
      return this.waitResultFlag ? "none" : "flex"
    },
    btnColor() {
      if (this.type === "SUCCESS") {
        return "aquamarine"
      } else if (this.type === "ERROR") {
        return "#ec5424"
      } else {
        return "white"
      }
    },
    getOffset() {
      if (this.offset <= 0) {
        return "- " + -this.offset + "px"
      } else {
        return "+ " + this.offset + "px"
      }
    }
  },
  watch: {
    waitResultFlag() {
      this.show = true
    }
  }
}
</script>

<style scoped>
#CompileResult {
  position: fixed;
  bottom: 0;
  height: 20vh;
  width: 100%;
  background: #313336;
  z-index: 1000;
  font-family: monospace;
}

#CompileResult.hide {
  top: calc(100vh - 40px);
}
</style>
<style scoped>
#CompileResult .output {
  padding-left: 20px;
  padding-right: 20px;
  /*
  需要js变化
   */
  height: calc(100% - 40px - 40px);
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
}

.output::-webkit-scrollbar {
  width: 12px;
  background-color: #313336;
}

.output::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
  background-color: #555;
}
.output::-webkit-scrollbar-corner {
  background-color: #313336;
}
#CompileResult .floor {
  height: 40px;
  line-height: 40px;
  text-align: center;
  font-size: 18px;
  color: white;
  font-family: 'Gotham SSm A', 'Gotham SSm B', 'Open Sans', Helvetica, Arial, sans-serif;
  border-top: 1px solid black;
}

#CompileResult span {
  font-family: inherit;
  line-height: normal;
  font-size: 20px;
  display: flex;
  white-space: pre;
  color: white;
}

#CompileResult span.standard {
  color: #afb1b3;
}

#CompileResult span.error {
  color: #ec5424;
}
</style>
<style scoped src="./css/ResultBtn.css"></style>
<style scoped>
.overlay {
  height: 100%;
  width: 100%;
  display: none;
}

.overlay.show {
  display: flex;
  justify-content: center;
  align-items: center;
}

.overlay > svg {
  width: 50px;
  height: 50px;
  animation: roll 1s infinite linear;
}

@keyframes roll {
  0% {
    transform: rotate(0)
  }
  50% {
    transform: rotate(180deg) scale(1.2)
  }
  100% {
    transform: rotate(360deg)
  }
}
</style>
<style scoped>
.topDrag {
  height: 40px;
  cursor: row-resize;
  display: flex;
  justify-content: center;
  align-items: center;
}
.topDrag.hide{
  cursor: default;
}
.line {
  height: 4px;
  width: 40px;
  background: #2b2b2b;
  margin: 4px 0 4px 0;
}
.lines.hide{
  opacity: 0;
}
</style>